.post-block {
	position: relative;
	margin-bottom: 30px;
	height: 300px;
	overflow: hidden;
	transition: all 0.2s linear 0s;
	background-size: cover !important;
	img {
		max-width: 100%;
		height: 100%;
		width: 100%;
	}
	&:before {
		content: "";
		width: 100%;
		height: 90px;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 10;
		background: rgba(0,0,0,0.8);
		background: -moz-linear-gradient(top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%);
		background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0,0,0,0.8)), color-stop(100%, rgba(0,0,0,0)));
		background: -webkit-linear-gradient(top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%);
		background: -o-linear-gradient(top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%);
		background: -ms-linear-gradient(top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%);
		background: linear-gradient(to bottom, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
	}
	.post-area {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 20;
		padding: 20px;
		display: block;
		outline: none;
		text-decoration: none;
		transition: all 0.2s linear 0s;
	}
	.post-title {
		color: #fff;
		font-weight: 700;
		font-size: 20px;
		text-transform: uppercase;
		line-height: 1.8;
	}
	.post-info {
		position: absolute;
		bottom: 0;
		left: 0;
		transition: all 0.2s linear 0s;
		ul {
			list-style: none;
			padding: 0;
			margin: 0;
			li {
				display: inline-block;
				margin-right: 25px;
				color: #fff;
				position: relative;
				font-size: 12px;
				font-weight: 500;
				span {
					margin-right: 5px;
				}
				&:after {
					content: '|';
					position: absolute;
					right: -16px;
					top: -1px;
				}
				&:last-child:after {
					display: none;
				}
			}
		}
	}
	.post-wrapp {
		position: relative;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding: 0;
		border: solid 1px transparent;
		transition: all 0.2s linear 0s;
	}
	&:hover {
		.post-area {
			background: rgba(0,0,0,.3);
		}
		.post-wrapp {
			border: solid 1px #d89354;
			padding: 25px;
		}
		.post-info {
			bottom: 25px;
			left: 25px;
		}
	}
}

// single posts style
.blog_area {
	background: #fff;
	padding: 30px;
	img {
		max-width: 100%;
	}
	p {
		margin: 25px 0;
		font-size: 16px;
		line-height: 1.8;
	}
	blockquote {
		padding: 30px 30px 30px 110px;
		border: solid 1px #dbdbdb;
		background: url('../img/blockquote.png') no-repeat 30px 30px;
		color: #a8aaac;
		font-size: 16px;
		font-style: italic;
		line-height: 1.6;
		margin: 40px 0;
	}
}
.post-title {
	text-transform: uppercase;
	color: #1f292f;
	font-size: 25px;
	font-weight: 700;
	margin: 0 0 20px;
}
.post-date {
	ul {
		list-style: none;
		padding: 0;
		margin: 0 0 25px;
		li {
			color: #a8aaac;
			display: inline-block;
			font-size: 13px;
			margin-right: 25px;
			position: relative;
			span {
				margin-right: 5px;
			}
			&:after {
				content: "|";
				position: absolute;
				right: -16px;
				top: -1px;
			}
			&:last-child:after {
				display: none;
			}
		}
	}
}
.share {
	ul {
		list-style: none;
		padding: 0;
		margin: 0;
		text-align: right;
		li {
			display: inline-block;
			margin-left: 5px;
			margin-bottom: 5px;
			a {
				display: block;
				width: 34px;
				height: 34px;
				outline: none;
			}
			&.telegramm-btn {
				a {
					@include sprite($s-telegramm-btn)
					&:hover {
						@include sprite($s-telegramm-btn-hover)
					}
