// styles for trainings
.we-train {
	padding: 90px 0;
	.page-logo {
		margin-bottom: 70px;
	}
}
.section .page-logo .page-title {
	color: #39393c;
	font-family: "intro_head";
	font-size: 40px;
}
.train-list {
	list-style: none;
	margin: 0 -5px;
	padding: 0;
	font-size: 0 !important;
	> li {
		vertical-align: top;
		display: inline-block;
		font-size: 16px;
		width: 25%;
		padding: 0 5px;
	}
}
.train-block {
	background: #fff;
	padding: 20px 20px 25px;
}
.train-img {
	text-align: center;
	img {
		height: 60px;
		margin: 40px 0 20px;
	}
	p {
		font-family: 'intro_head';
		font-size: 27px;
		text-transform: uppercase;
		color: #d89455;
		margin: 0;
	}
}
.train-title {
	margin: 30px 0 20px;
	font-weight: 700;
	text-align: center;
	padding: 15px 0;
	border-width: 1px 0;
	border-color: #ebebeb;
	border-style: solid;
	text-transform: uppercase;
}
.list-of {
	list-style: none;
	padding: 0;
	margin: 0;
	li {
		display: block;
		font-weight: 300;
		padding: 0 0 0 35px;
		position: relative;
		line-height: 35px;
		&:before {
			content: '';
			position: absolute;
			top: 10px;
			left: 10px;
			width: 13px;
			height: 12px;
			background: url("../img/train-check.png") no-repeat 0 0;
		}
	}
}
// End styles for trainings

// Styles for when-tocall block
.when-tocall {
	background: #fff;
	padding: 60px 0;
}
.call-list {
	list-style: none;
	padding: 0;
	margin: 0;
	li {
		position: relative;
		padding-left: 65px;
		font-size: 20px;
		font-weight: 300;
		margin-bottom: 50px;
		&:last-child {
