}
.call-number {
	position: absolute;
	top: 50%;
	margin-top: -24px;
	left: 0;
	width: 48px;
	height: 48px;
	border-radius: 50%;
	border: solid 1px #d89354;
	font-size: 20px;
	text-align: center;
	line-height: 46px;
	font-weight: normal;
	color: #d89354;
}
// End styles for when tocall
// Styles for why works block
.why-works {
	padding: 60px 0;
}
.why-list > div {
	padding-bottom: 30px;
}
.why-block {
	position: relative;
	cursor: pointer;
	&:before {
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		border: 20px solid transparent;
		box-shadow: inset 0 0 0 1px transparent;
		transition: all 0.2s linear 0s;
		background: transparent;
	}
	&:hover {
		&:before {
			box-shadow: inset 0 0 0 1px #d89354;
			background: rgba(0,0,0,0.2);
		}
	}
	img {
		max-width: 100%;
	}
	.why-text {
		position: absolute;
		width: 100%;
		text-align: center;
		top: 50%;
		transform: translateY(-50%);
		color: #fff;
		font-weight: 700;
		font-size: 20px;
		padding: 0 20px;
		text-transform: uppercase;
	}
}
.ref-btn {
	text-align: center;
}
// End styles for why works block
