// Styles for training-calendar
.training-calendar {
	padding: 50px 0;
	background: url('../img/calendar-bg.jpg') no-repeat center center;
	background-size: cover;
	.page-logo .page-title {
		color: #fff;
	}
}
.calendar-block {
	padding: 30px;
	margin-bottom: 30px;
	background: #fff;
	> a {
		display: block;
		outline: none;
		text-decoration: none;
		color: #16242f;
		&:hover {
			.calendar-title {
				color: #d89354;
			}
			.read-more {
				color: #fff;
				background: #e5a870;
			}
		}
	}
	.read-more {
		color: #fff;
		background: #d89354;
		transition: all 0.2s linear 0s;
	}
	.calendar-title {
		line-height: 35px;
		margin: 0 0 30px;
		font-size: 20px;
		font-weight: 700;
		text-transform: uppercase;
		transition: all 0.2s linear 0s;
	}
	.calendar-text {
		line-height: 35px;
		font-weight: 300;
		margin: 0 0 30px;
	}
	.date {
		color: rgba(22,36,47,0.7);
		padding: 8px 0;
		i {
			margin-right: 15px;
			position: relative;
			top: 1px;
		}
