}
// End styles for training-calendar
// Styles for trainer
.about-trainer {
	background: url("../img/popular-bg.jpg") no-repeat center center;
	overflow: hidden;
	padding: 110px 0 0;
	.container {
		position: relative;
		min-height: 460px;
	}
}
.trainer-logo {
	display: block;
	position: absolute;
	bottom: 0;
	left: 15px;
}
.about-content {
	padding-bottom: 60px;
}
.about-title {
	font-size: 40px;
	text-transform: uppercase;
	margin: 0 0 40px;
	font-family: 'intro_head';
}
.about-social {
	margin-bottom: 40px;
	> span {
		text-transform: uppercase;
		font-size: 24px;
		color: #d89354;
		display: inline-block;
		margin-right: 20px;
		vertical-align: middle;
	}
	ul {
		display: inline-block;
		list-style: none;
		vertical-align: middle;
		padding: 0;
		margin: 0;
		li {
			display: inline-block;
			margin: 0 4px;
			vertical-align: top;
			a {
				display: block;
				width: 28px;
				height: 28px;
				text-align: center;
				position: relative;
				transition: box-shadow 0.2s ease 0s;
				text-align: center;
				&:after {
					box-sizing: content-box;
					content: "";
					height: 100%;
					pointer-events: none;
					position: absolute;
					width: 100%;
					box-shadow: 0 0 0 1px #d89354;
					left: 0;
					padding: 0;
					top: 0;
					transition: transform 0.2s ease 0s;
				}
				&:hover {
					box-shadow: 0 0 0 3px rgba(216, 147, 84, 0.4);
					&:after {
						transform: scale(0.8);
						box-shadow: 0 0 0 2px #d89354;
					}
				}
				span {
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%,-50%);
				}
			}
			&.foot-linked {
				a span {
					@include sprite($s-linked-hover-ico);
				}
			}
			&.soc-skype {
				a span {
					@include sprite($s-miniskype-ico);
				}
			}
			&.soc-mail {
				a span {
					@include sprite($s-minimail-ico);
				}
			}
		}
	}
}
.about-text {
	font-weight: 300;
	font-size: 17px;
	line-height: 35px;
}
