// Styles for our team
.our-team {
	padding: 90px 0;
	ul.personal-grid {
		list-style: none;
		padding: 0;
		margin: 0 -5px;
		font-size: 0 !important;
		> li {
			font-size: 16px;
			display: inline-block;
			vertical-align: top;
			padding: 0 5px 10px;
			width: 25%;
		}
	}
}
.team-block {
	overflow: hidden;
	position: relative;
	max-height: 360px;
	cursor: pointer;
	img {
		max-width: 100%;
	}
	&:before {
		content: "";
		width: 100%;
		height: 100%;
		display: block;
		border: 10px solid transparent;
		box-shadow: inset 0 0 0 1px #d89354;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 5;
		opacity: 0;
		transition: all 0.2s linear 0s;
	}
	&:after {
		content: "";
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 270px;
		background: rgba(255,255,255,0);
		background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(0,0,0,0.7) 100%);
		background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(255,255,255,0)), color-stop(100%, rgba(0,0,0,0.7)));
		background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(0,0,0,0.7) 100%);
		background: -o-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(0,0,0,0.7) 100%);
		background: -ms-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(0,0,0,0.7) 100%);
		background: linear-gradient(to bottom, rgba(255,255,255,0) 0%, rgba(0,0,0,0.7) 100%);
		opacity: 0;
		transition: all 0.2s linear 0s;
	}
	&:hover {
		&:after, &:before {
			opacity: 1;
		}
		.team-info {
			bottom: 0;
		}
	}
}
.team-info {
	position: absolute;
	bottom: -58px;
	left: 0;
	width: 100%;
	text-align: center;
	z-index: 10;
	transition: all 0.2s linear 0s;
}
.team-name {
	text-transform: uppercase;
	color: #fff;
	font-size: 30px;
	font-family: 'intro_head';
	margin: 0 0 15px;
}
.team-position {
	color: #d89354;
	font-size: 17px;
	margin: 0 0 20px;
}
.team-socials {
	list-style: none;
	padding: 0;
	margin: 0 0 20px;
	width: 100%;
	li {
		margin: 0 3px 0;
		display: inline-block;
		a {
			display: block;
			width: 32px;
			height: 32px;
			background: #d89354;
			text-align: center;
			position: relative;
			span {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}
}
