.buttons {
	text-align: center;
	padding-top: 45px;
	a {
		display: inline-block;
		padding: 15px;
		width: 135px;
		height: 44px;
		text-align: center;
		font-weight: 700;
		font-size: 14px;
		color: #fff;
		outline: none;
		background: url('../img/page-btn-bg.png') no-repeat 0 0;
		&:hover {
			background-position: 0 -44px;
			color: #16242f;
			text-decoration: none;
		}
	}
}
.contacts-row {
	> div {
		float: left;
		width: 50%;
	}
}
#map {
	height: 900px;
}
.contact-form {
	padding: 50px 20px 50px 50px ;
	background: url("../img/popular-bg.jpg");
	height: 900px;
	form {
		width: 430px;
	}
	p {
		line-height: 1.8;
		margin: 30px 0;
	}
	ul {
		list-style: none;
		padding: 0;
		margin: 40px 0;
		li {
			display: block;
			margin-bottom: 30px;
			color: #16242f;
			a {
				color: #16242f;
			}
			span {
				margin-right: 15px;
				vertical-align: middle;
			}
			&:last-child {
				margin: 0;
			}
		}
	}
}
.form-title {
	text-transform: uppercase;
	color: #39393c;
	font-size: 30px;
	margin: 0 0 30px;
	font-family: $headers-font;
}
.form-group {
	margin-bottom: 20px;
}
.form-control {
	width: 100%;
	height: 44px;
	border: solid 1px #d89254;
	padding: 0 0 0 20px;
	background: none;
	outline: none;
}
textarea.form-control {
	height: 130px;
	padding-top: 20px;
	resize: none;
}
.send-msg {
	width: 100%;
	height: 44px;
	color: #fff;
	font-weight: 700;
	border: 0;
	outline: 0;
	background: #d89254;
	transition: all 0.2s linear 0s;
	&:hover, &:focus {
		color: #fff;
		background: #e5a870;
	}
}
.contacts footer {
	background: #f8f8f8;
}