header {
	background: $header-bg-color;
	.nav {
		// padding-top: 28px;
		// padding-bottom: 28px;
	}
}
.nav {
	list-style: none;
	margin: 0;
	padding: 0;
	li {
		display: block;
		float: left;
		margin-left: 45px;
		a {
			font-family: $nav-font;
			text-transform: uppercase;
			color: $main-font-color;
			font-size: 14px; 
			&:hover, &.active {
				color: $hover-nav-color;
				text-decoration: none;
			}
		}
		&:first-child {
			margin: 0;
		}
	}
}
.lang-nav {
	padding-left: 45px;
	li {
		margin-left: 10px;
	}
}
.logo {
	display: block;
	padding: 22px 0;
	height: 70px;
	img {
		max-width: 100%;
	}
}
.collapse {
	display: none;
}
.navbar-toggle {
	outline: none;
	border: solid 1px #eaeaea;
	background: #FFF;
	padding: 9px 10px;
	border-radius: 4px;
	margin: 12px 0;
	.icon-bar {
		border-radius: 1px;
		display: block;
		height: 2px;
		width: 22px;
		background-color: #888;
	}
	&:hover {
		background: #d89354;
		.icon-bar {
			background: #FFF;
		}
	}
}
.navbar-toggle .icon-bar + .icon-bar {
	margin-top: 4px;
}
.sr-only {
	border: 0 none;
	clip: rect(0px, 0px, 0px, 0px);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

// Styles for banner
.banner {
	// padding: 90px 0 0;
	height: 355px;
	background-size: cover !important;
	background-position: center center !important;
	position: relative;
	-moz-user-select: none;
	-o-user-select:none;
	-khtml-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	 user-select: none;
	.container {
		z-index: 100;
		position: relative;
		height: 100%;
		.row {
			height: 100%;
			> div {
				position: absolute;
				top: 50%;
				width: 100%;
				transform: translateY(-50%);
			}
		}
	}
	&:before, &:after {
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background: #16242f;
		opacity: 0.12;
	}
	&:after {
		opacity: 0.2;
		background: #030406;
	}
}
.page-info {
	font-family: $headers-font;
	font-size: 40px;
	color: #fff;
	text-transform: uppercase;
	text-align: center;
	margin: 0;
}
.page-logo {
	margin-bottom: 40px;
	text-align: center;
	.page-img {
		margin-bottom: 15px; 
	}
	.page-title {
		color: #fff;
		display: inline-block;
		text-transform: uppercase;
		font-family: $page-title-font;
		font-size: 37px;
		position: relative;
		line-height: 1;
		&:before, &:after {
			content: "";
			width: 102px;
			height: 3px;
			background: url("../img/title-line.png") no-repeat 0 0;
			position: absolute;
			top: 50%;
			margin-top: -1.5px;
		}
		&:before {
			left: -125px;
		}
		&:after {
			right: -125px;
			transform: rotate(180deg);
		}
	}
}