.pager {
	padding: 70px 0;
	font-family: $nav-font;
	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		text-align: center;
		display: inline-block;
		position: relative;
		top: -19px;
		li {
			display: inline-block;
			font-size: 15px;
			a {
				padding: 10px;
				color: #16242f;
				text-decoration: none;
				&:hover, &.active {
					color: $hover-nav-color;
				}
			}
		}
	}
}
.post-nav {
	a {
		color: #16242f;
		font-size: 14px;
		&:hover {
			text-decoration: none;
			color: $hover-nav-color;
		}
		span.s-l-arrow {
			margin-right: 15px;
			vertical-align: middle;
			top: -2px
		}
		span.s-r-arrow {
			margin-left: 15px;
			vertical-align: middle;
			top: -2px
		}
	}
}