.popular-posts {
	.page-logo .page-title {
		font-family: $headers-font;
		font-size: 30px;
		color: #39393c;
	}
	background: url('../img/popular-bg.jpg') center center;
	padding: 68px 0 38px;
	.post-block {
		height: 190px;
		.post-title {
			font-size: 15px;
		}
		&:hover .post-wrapp {
			border-color: transparent;
			padding: 0;
		}
		&:hover .post-info {
			bottom: 0;
			left: 0;
		}
		&:hover .post-area {
			background: rgba(0, 0, 0, 0.3) none repeat scroll 0 0;
		}
	}
}
.popular-posts.similar-posts {
	background: none;
}