.vacancy {
	ul.vac-list {
		list-style: none;
		margin: 0;
		padding: 0;
		> li {
			display: block;
			margin-bottom: 30px;
			background: #FFF;
			border: solid 1px transparent;
			&:hover {
				border-color: #d89354;
				.read-more {
					background: #D89354;
					color: #fff;
				}
				.vacancy-title {
					color: #d89354;
				}
			}
			> a {
				display: block;
				padding: 30px;
				outline: none;
				&:hover, &:focus {
					color: inherit;
					text-decoration: none;
				}
			}
		}
	}
}
.date-line {
	margin: 0 0 25px;
	font-size: 14px;
	span {
		text-transform: uppercase;
		color: rgba(22,36,47,.7);
	}
}
.vacancy-title {
	color: #16242f;
	text-transform: uppercase;
	margin: 0 0 25px;
	font-weight: 700;
	font-size: 20px;
}
.vac-info {
	list-style: none;
	padding: 0;
	margin: 25px 0;
	li {
		display: inline-block;
		margin-right: 25px;
		font-size: 15px;
		color: rgba(22,36,47,.7);
		span {
			margin-right: 10px;
			vertical-align: top;
		}
		a {
			color: rgba(22,36,47,.7);
		}
	}
}
.read-more {
	display: inline-block;
	font-size: 13px;
	color: #333;
	text-transform: uppercase;
	padding: 10px 15px;
	font-weight: 700;
	border: solid 1px #d89354;
	&:hover {
		color: #333;
		text-decoration: none;
	}
}

// styles for referal
.referal {
	background: url('../img/popular-bg.jpg') center center;
	padding: 60px 0 40px;
	text-align: center;
}
.ref-title {
	color: #16242f;
	font-size: 37px;
	font-family: $headers-font;
	text-transform: uppercase;
	margin: 0 0 30px;
}
.ref-info {
	font-size: 20px;
	color: #16242f;
	font-weight: 300;
	line-height: 1.8;
	margin: 0 0 40px;
}
.ref-btn {
	display: inline-block;
	width: 272px;
	height: 44px;
	color: #d89354;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 700;
	background: url('../img/ref-btn-bg.png') no-repeat 0 0;
	padding: 14px;
	&:hover, &:focus {
		text-decoration: none;
		color: #fff;
		background-position: 0 -44px;
	}
}
// styles for single vacancy
.single-vac {
	padding: 30px;
	.vac-info {
		margin-bottom: 0;
	}
	.single-info {
		width: 65%;
		padding-right: 20px;
	}
	.single-person {
		width: 35%;
	}
	&:hover {
		border-color: transparent !important;
		.vacancy-title {
			color: #16242f !important;
		}
	}
}
.person-block {
	position: relative;
	padding-left:  115px;
	height: 95px;
	width: 310px;
	float: right;
	.align-info {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
	}
	a {
		display: inline-block;
		padding: 5px 15px;
		border: solid 1px #dd4a40;
		color: #dd4a40;
		border-radius: 20px;
		outline: none;
		font-size: 13px;
		margin: 0 5px 0 0;
		&:hover, &:focus {
			background: #dd4a40;
			color: #fff;
			text-decoration: none;
		}
		&.p-skype {
			border-color: #00adef;
			color: #00adef;
			&:hover, &:focus {
				background: #00adef;
				color: #fff;
			}
		}
	}
}
.p-img {
	width: 95px;
	height: 95px;
	overflow: hidden;
	text-align: center;
	border-radius: 50%;
	position: absolute;
	left: 0;
	top: 0;
	margin: 0;
}
.p-name {
	font-weight: 600;
	font-size: 17px;
	margin: 0 0 5px;
}
.p-vac {
	color: #7a7f83;
	font-size: 15px;
}
.vac-content {
	padding: 30px;
	background: #fff;
	.vacancy-title {
		margin: 0 0 10px;
	}
	p {
		line-height: 2;
		margin-bottom: 20px;
	}
}
.vacancy-control {
	padding-top: 10px;
}
.brown-btn {
	padding: 10px 30px;
	color: #fff;
	background: #d89354;
	text-transform: uppercase;
	display: inline-block;
	font-size: 17px;
	transition: all 0.2s linear 0s;
	&:hover, &:focus {
		color: #fff;
		background: #e5a870;
		text-decoration: none;
	}
}
.modal-dialog {
	width: 660px;
}
.modal-content {
	border: 0;
	border-radius: 0;
	box-shadow: none;
	background: url('../img/popular-bg.jpg');
}
.modal-header {
	border-bottom: 0;
}
.modal-title {
	font-size: 30px;
	text-align: center;
	font-family: $headers-font;
	text-transform: uppercase;
	padding: 30px 10px 10px;
}
.modal-header .close {
	position: absolute;
	right: 20px;
	top: 10px;
	margin: 0;
	font-size: 30px;
	color: #16242f;
	opacity: 1;
	&:hover {
		color: #d89254;
	}
}
.modal-body {
	.contact-form {
		height: auto;
		background: #FFF;
		padding: 20px;
		width: 430px;
		margin: 0 auto 40px;
		form {
			width: auto;
		}
	}
}