.category {
	padding: 70px 0 50px;
	height: 152px;
	.nav {
		// padding-top: 5px;
		li {
			margin-left: 25px;
			&:first-child {
				margin: 0;
			}
		}
	}
}
.search-form {
	text-align: right;
}
.input-control {
	position: relative;
	display: inline-block;
	input[type="text"] {
		outline: none;
		padding: 0 30px 10px 0;
		background: none;
		border: 0;
		border-bottom: solid 1px #dbdbdb;
		color: #1c2931;
		font-family: $nav-font;
		font-size: 14px;
		text-transform: uppercase;
	}
	button {
		position: absolute;
		right: 0;
		top: 2px;
		background: none;
		border: 0;
		outline: none;
		padding: 0;
	}
}