footer {
	padding: 40px 0;
	background: $main-bg-color;
	font-family: $nav-font;
	font-size: 14px;
	p {
		margin: 0;
	}
	.logo {
		padding: 0;
		height: auto;
	}
	.copy {
		font-size: 12px;
		text-transform: uppercase;
		margin: 20px 0;
	}
	.shvets {
		font-size: 14px;
		font-weight: 300;
		font-family: 'Open Sans', sans-serif;
		img {
			vertical-align: baseline;
		}
	}
	ul {
		list-style: none;
		margin: 0;
		padding: 20px 0 0;
		text-align: center;
		li {
			display: inline-block;
			margin: 0 4px;
			vertical-align: top;
			a {
				display: block;
				width: 28px;
				height: 28px;
				text-align: center;
				position: relative;
				transition: box-shadow 0.2s ease 0s;
				&:after {
					box-sizing: content-box;
					content: "";
					height: 100%;
					pointer-events: none;
					position: absolute;
					width: 100%;
					box-shadow: 0 0 0 1px #1f292f;
					left: 0;
					padding: 0;
					top: 0;
					transition: transform 0.2s ease 0s;
				}
				&:hover {
					box-shadow: 0 0 0 3px rgba(216, 147, 84, 0.4);
					&:after {
						transform: scale(0.8);
						box-shadow: 0 0 0 2px #d89354;
					}
				}
				span {
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
				}
			}
			&.foot-face {
				a span {
					@include sprite($s-face-ico);
				}
				&:hover span {
					@include sprite($s-face-hover-ico);
				}
			}
			&.foot-twitt {
				a span {
					@include sprite($s-twitt-ico);
				}
				&:hover span {
					@include sprite($s-twitt-hover-ico);
				}
			}
			&.foot-linked {
				a span {
					@include sprite($s-linked-ico);
				}
				&:hover span {
					@include sprite($s-linked-hover-ico);
				}
			}
			&.foot-insta {
				a span {
					@include sprite($s-insta-ico);
				}
				&:hover span {
					@include sprite($s-insta-hover-ico);
				}
			}
		}
	}
}
.blog_single footer {
	background: #FFF;
}