@media all and (max-width: $large) {
	.graph-year li p {
		margin: 0 !important;
	}
}
@media all and (max-width: $medium) {
	.logo {
		display: block;
		padding: 20px 0 0;
		height: auto;
		text-align: center;
		width: 100%;
	}
	.post-block {
		height: 240px;
	}
	.post-block .post-title {
		font-size: 16px;
	}
	.contacts-row > div {
		float: none;
		width: 100%;
	}
	.contact-form {
		height: auto;
	}
	#map {
		height: 400px;
	}
	.contact-form form {
		margin: 0 auto;
	}
	.single-vac .single-person {
		width: 48%;
	}
	.single-vac .single-info {
		width: 52%;
	}
	.banner {
		.container {
			height: auto;
		}
		.container .row {
			height: auto;
		}
		.container .row > div {
			position: static;
			transform: none;
		}
		height: auto;
		padding: 70px 0;
	}
	.train-list > li {
		width: 50%;
		margin-bottom: 10px;
	}
	.why-block .why-text {
		font-size: 15px;
	}
	.download-block p {
		font-size: 25px;
	}
	.trainer-logo {
		display: none;
	}
	.about-trainer {
		padding: 40px 0;
	}
	.about-content {
		padding: 0;
	}
	.about-trainer .container {
		min-height: auto;;
	}
	.our-team ul.personal-grid > li {
		width: 33.333%;
	}
	.graph-year li p {
		max-width: 100%;
		float: none !important;
		text-align: center !important;
	}
	.graph-year li {
		padding-top: 70px;
		margin-bottom: 100px;
	}
	.graph-year li:after {
		height: 70px;
		bottom: -90px;
	}
	.year {
		transform: none;
		top: -10px;
		transform: translateX(-50%);
	}
	.teams-expertise ul li {
		width: 50%;
	}
}
@media all and (min-width: 768px) {
	.navbar-collapse.collapse {
		display: inline-block !important;
		height: auto !important;
		overflow: visible !important;
		vertical-align: middle;
	}
	.align-menu {
		position: relative;
		height: 70px;
		&:after {
			content: '';
			display: inline-block;
			vertical-align: middle;
			height: 100%;
		}
		.nav {
			display: inline-block !important;
			vertical-align: middle;
		}
	}
}
@media all and (max-width: $small) {
	.teams-expertise {
		padding: 40px 0;
	}
	.teams-expertise .page-logo {
		margin-bottom: 20px;
	}
	.post-block {
		height: 170px;
		background: rgba(0,0,0,1);
		// img {
		// 	display: none;
		// }
		&:hover {
			background: rgba(0,0,0,0.8);
		}
	}
	.post-block:hover .post-wrapp {
		border: 1px solid transparent;
		padding: 0;
	}
	.post-block:hover .post-info {
		bottom: 0;
		left: 0;
	}
	.banner {
		padding: 30px 0;
	}
	.page-info {
		font-size: 25px;
	}
	.page-logo {
		margin-bottom: 20px;
	}
	.page-logo .page-title:before,
	.page-logo .page-title:after {
		display: none;
	}
	.navbar-collapse {
		position: absolute;
		top: 100%;
		width: 100%;
		left: 0;
		background: $header-bg-color;
		z-index: 101;
		ul {
			margin-top: 20px;
			border-top: solid 1px #eaeaea;
		}
		li {
			float: none;
			margin: 0;
			a {
				display: block;
				padding: 15px;
				&:hover, &.active {
					background: #eaeaea; 
				}
			}
		}
	}
	.logo {
		width: auto;height: auto;
		padding: 16px 0;
	}
	.lang-nav {
		// padding-top: 20px;
		padding-right: 20px;
		padding-left: 0;
		height: 58px;
    line-height: 58px;
	}
	.category {
		padding: 30px 0;
		height: auto;
	}
	.category .nav {
		padding: 0;
		margin-bottom: 20px;
		li {
			display: block;
			margin: 0;
			float: none;
			text-align: center;
			padding: 5px 0;
		}
	}
	.search-form {
		.input-control {
			display: block;
			input[type="text"] {
				width: 100%;
			}
		}
	}
	.popular-posts {
		padding: 30px 0;
	}
	.blog_area {
		padding: 10px;
	}
	.blog_area blockquote {
		background: #FFF;
		padding: 10px;
	}
	.blog_area p {
		font-size: 14px;
	}
	.share ul {
		text-align: left;
	}
	.contact-form form {
		margin: 0;
		width: 100%;
	}
	.contact-form {
		padding: 15px 15px 30px;
	}
	.vacancy ul.vac-list > li {
		padding: 15px;
	}
	.vacancy-title {
		font-size: 16px;
	}
	.ref-title {
		font-size: 25px;
	}
	.single-vac .single-person {
		width: 100%;
		float: none !important;
	}
	.single-vac .single-info {
		width: 100%;
		float: none !important;
		padding: 0 0 20px;
	}
	.vacancy-control {
		> div {
			width: 100%;
			float: none !important;
		}
	}
	.person-block {
		float: none;
	}
	.vac-content {
		padding: 15px;
		.share {
			padding-top: 20px;
		}
	}
	.vacancy-control {
		text-align: center;
		.share ul {
			text-align: center;
		}
	}
	.modal-dialog {
		width: auto;
		margin: 15px;
	}
	.modal-title {
		font-size: 24px;
	}
	.modal-body .contact-form {
		padding: 0;
		width: 100%;
	}
	header .nav {
		margin: 0;
	}
	.navbar-collapse ul {
		padding: 0;
	}
	.we-train, .when-tocall, .why-works {
		padding: 40px 0;
		.page-logo {
			margin-bottom: 40px;
		}
	}
	.d-separate:after {
		display: none;
	}
	.download-block {
		padding: 20px 0;
	}
	.download-block p {
		margin-bottom: 20px;
	}
	.dwld-area {
		padding: 20px 0;
	}
	.calendar-block .calendar-title, .calendar-block .calendar-text {
		font-size: 18px;
		line-height: 20px;
		margin-bottom: 20px;
	}
	.calendar-block .calendar-text {
		font-size: 16px;
	}
	.training-calendar {
		padding: 30px 0 0;
	}
	.about-title, .about-social {
		margin-bottom: 20px;
	}
	.members-feedback {
		padding: 40px 0;
	}
	.members-feedback .page-logo {
		margin-bottom: 20px;
	}
	.feedback-block {
		padding: 20px;
	}
	.our-team ul.personal-grid > li {
		width: 50%;
	}
	.our-team {
		padding: 40px 0;
	}
	.we-started {
		padding: 40px 0;
	}
	.we-started .page-logo {
		margin-bottom: 20px;
	}
	.started-description {
		margin-bottom: 20px;
		font-size: 20px;
	}
	.section .page-logo .page-title {
		font-size: 30px;
	}
	.cl-contacts {
		padding: 40px 0;
	}
	.vacancy ul.vac-list > li > a {
		padding: 0;
	}
	.referal {
		padding: 40px 0;
	}
}
@media all and (max-width: 540px) {
	.date-line span {
		display: block;
		float: none !important;
	}
	.vac-info li {
		display: block;
		margin: 0 0 5px;
	}
	#map {
		height: 300px;
	}
	.posts .row > div, .popular-posts .row > div {
		width: 100%;
	}
	.pager {
		padding: 40px 0;
	}
	.popular-posts .post-block {
		height: 170px;
	}
	.pager ul {
		top: 0;
		padding-top: 20px;
		display: block;
	}
	.p-img {
		width: 80px;
		height: 80px;
	}
	.person-block {
		width: auto;
		height: auto;
		padding-left: 90px;
	}
	.person-block a {
		margin-right: 0;
	}
	.person-block .align-info {
		position: static;
		transform: none;
	}
	.train-list > li {
		width: 100%;
	}
	.why-list > div {
		width: 100%;
	}
	.why-block img {
		width: 100%;
	}
	.why-block .why-text {
		font-size: 20px;
	}
	.teams-expertise ul li {
		width: 100%;
	}
}
@media all and (max-width: 460px) {
	.our-team ul.personal-grid > li {
		width: 100%;
	}
}